<template>
    <div :class="clases ?? []">
        <label class=" fw-semibold d-block" :for="id"><code v-if="obligatorio ?? false">*</code>{{nombre}}</label>
        <div class="col-md">
            <select
                class="form-select" 
                :value="opcion ?? 'Seleccione'" 
                :id="id" 
                aria-label="Default select"
                @input="updatedOpcion" 
                :disabled="(showVer ?? false) || loading || opciones.length < 1 || disabled">
                <option :value="null" selected>Seleccione</option>
                <template v-for="item in opciones" :key="item">
                    <option :value="item" :selected="item===opcion">{{item}}</option>
                </template>
            </select>
        </div>
    </div>
</template>
<script>
import { defineComponent } from 'vue'
export default defineComponent({
    props:[
        'clases', 
        'opciones', 
        'nombre', 
        'id', 
        'opcion', 
        'showVer', 
        'loading', 
        'obligatorio', 
        'disabled'
    ],
    setup(props, { emit }) {
        const updatedOpcion = (event) => {
            let value = event.target.value === 'Seleccione' ? null : event.target.value;
            emit('update:opcion', value);
        }
        return{
            updatedOpcion
        }
    },
})
</script>
